*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  font-size: 62.5%; }
  @media only screen and (max-width: 75em) {
    html {
      font-size: 56.25%; } }
  @media only screen and (max-width: 56.25em) {
    html {
      font-size: 50%; } }
  @media only screen and (max-width: 37.5em) {
    html {
      font-size: 37.5%; } }
  @media only screen and (min-width: 112.5em) {
    html {
      font-size: 75%; } }

body {
  font-weight: 300; }

.navbar {
  width: 100vw;
  background-color: white;
  position: fixed; }
  .navbar .first {
    display: flex;
    justify-content: flex-end; }
    .navbar .first li {
      list-style-type: none;
      margin: 0 1vw;
      font-size: 1.5rem;
      font-family: Apercu,sans-serif;
      color: #666666;
      padding: 1.3rem .5rem; }
      .navbar .first li a {
        text-decoration: none;
        color: black; }
        .navbar .first li a:hover {
          text-decoration: underline; }

.home {
  background-color: white;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: 'Roboto', sans-serif; }
  .home--main div {
    text-align: center;
    margin-top: 15rem; }
    .home--main div a {
      color: #444444;
      text-decoration: none;
      font-size: 3rem;
      border-bottom: solid #CCCCCC .1rem;
      padding: 1rem 3rem; }
  .home--primary {
