          .projects--p4--content div a:hover, .projects--p3--content div a:hover, .projects--p2--content div a:hover, .projects--p1--content div a:hover {
            background-color: #a4a4a5; }
          @media only screen and (max-width: 600px) {
            .projects--p4--content div a, .projects--p3--content div a, .projects--p2--content div a, .projects--p1--content div a {
